@import "mixins";
@import "varibles";

html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
  
}

// ScrollHide

body {
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  font-size: $font-medium ;
  color: $gray;
  
}

ul {
  list-style: none;
}

a, a:visited, a:hover {

  text-decoration: none;
}

// Text
h1, h2, h3 {
  margin: 0;
}

p{
  margin: 0;
  font-weight: 500;
}

button{
  color:#fff;
  background-color: $cyan;
  padding: 1.25rem 2.5rem;
  border: none;
  border-radius: 50px;
  font-size: $font-medium;
  font-weight: 700;
  cursor: pointer;
}


//Spacing

.container {
  max-width: 69.375rem;
  margin: 0 auto;

}

//FlexContainer

.flex {
  display: flex;

  &-jc-sb {
    justify-content: space-between;
  }

  &-jc-c {
    justify-content: center;
  }

  &-ai-c {
    align-items: center;
  }

}


//Visibility

.hide-for-mobile {
  @include breakpoint-down(small) {
    display: none;
  }

}

.hide-for-desktop {
  @include breakpoint-up(medium) {
    display: none;
  }
}